.App {
  font-family: sans-serif;
  text-align: center;
}

img{
  width: 80%;
  height: 100%;
}

.main{
  width: 100%;
  display: flex;
  flex-direction: row;
}

.dino-name-list{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.dino-profile{
  
}

.progress-fill{
  position: absolute;
  transition: width linear 0.5s;
  width: 0px;
  height: 100%;
  top: 0px;
  background-color:lightcoral;
  z-index: -1;
  margin-left: -5px;
}

.dino-name-container{
  /* font-family: "Gill Sans", sans-serif; */
  font-size: 16px;
  /* padding-left: 5px; */
  /* text-align: right; */
  border-right: solid 5px lightgreen;
  position: relative;
  /* width: auto; */
  display: flex;
  align-items: center;
  height: 30px;
}

.dino-name{
  position: relative;
  padding-right: 5px;
  padding-left: 5px;
  height: 100%;
  display: flex;
  align-items: center;
}

.dino-name-text::first-letter{
  font-weight: bold;
  /* background-color: lightgreen; */
}

.dino-name-selected{
  border-right: solid 4px lightcoral;
}

.dino-name-container:hover .progress-fill, 
.dino-name-selected .progress-fill {
  width: 100%;
}